import { Box, Typography } from '@mui/material';
import { useState, useEffect } from 'react';

const About = () => {
  const [scrollY, setScrollY] = useState(0);

  useEffect(() => {
    if (window.innerWidth > 1200) {
      const handleScroll = () => setScrollY(window.scrollY);
      window.addEventListener('scroll', handleScroll);
      return () => window.removeEventListener('scroll', handleScroll);
    }
    if (window.innerWidth < 1200) {
      setScrollY(0);
    }
  }, []);

  return (
    <div className='AboutContent'>
      <Box className='ContentBox'>
        <Box className={scrollY > 550 ? 'Fixed' : 'TextBoxAbout'}>
          <Typography variant='h6' sx={{ paddingBottom: 1 }}>
            You can download my CV{' '}
            <a
              className='AboutLinks'
              href='../Filip-Brady-CV.pdf'
              download='Filip-Brady-CV.pdf'
            >
              here.
            </a>
          </Typography>
          <Typography variant='h6'>
            In the present I am working on a start-up as a manager of an
            advertising site. My work is entirely based on self-studies, as I
            learned to run Google Ads, Meta Ads, design posts for social media
            in Figma, analyse data from Google Analytics and code purely by
            myself.
          </Typography>
          <Typography variant='h6'>
            Regardless of my current position, however, I have always been more
            interested in coding and creating websites. Currrently, I am a
            Junior Developer learning ReactJS. You can see my latest projects in
            "My Projects" section. I am located in Maastricht, the Netherlands,
            yet I have no problem working from home.
          </Typography>
          <Typography variant='h6' sx={{ paddingTop: 2 }}>
            In case you like my work and are interested in working with me,
            please contact me through my{' '}
            <a
              className='AboutLinks'
              href='mailto:filipbrady@gmail.com'
              target={'_blank'}
              rel='noreferrer'
            >
              Email
            </a>
            ,{' '}
            <a
              className='AboutLinks'
              href='https://www.linkedin.com/in/filip-br%C3%A1dy-332560237/'
              target={'_blank'}
              rel='noreferrer'
            >
              LinkedIn
            </a>{' '}
            or{' '}
            <a
              className='AboutLinks'
              href='https://www.facebook.com/filip.brady/'
              target={'_blank'}
              rel='noreferrer'
            >
              Facebook
            </a>
            . You can view my work on my{' '}
            <a
              className='AboutLinks'
              href='https://github.com/FilipBrady'
              target={'_blank'}
              rel='noreferrer'
            >
              Github
            </a>
            {'.'}
          </Typography>
        </Box>
      </Box>
    </div>
  );
};
export default About;
