import { Tooltip, Typography } from '@mui/material';

type TooltipProps = {
  hrefLink: string;
  TooltipIcon: any;
  TooltipTitle: string;
};

const ProjectTooltip = ({
  hrefLink,
  TooltipIcon,
  TooltipTitle,
}: TooltipProps) => {
  return (
    <Tooltip
      title={TooltipTitle}
      placement='right'
      enterDelay={10}
      leaveDelay={500}
      className='GithubToolTip'
    >
      <Typography
        variant='h1'
        sx={{
          marginY: 2,
          width: 'fit-content',
          marginX: 'auto',
        }}
      >
        {' '}
        <a
          href={hrefLink}
          className='LinkGithubProject'
          target='_blank'
          rel='noreferrer'
        >
          {TooltipIcon}
        </a>{' '}
      </Typography>
    </Tooltip>
  );
};
export default ProjectTooltip;
